<template>
  <div class="event-detail">
    <!-- ËøîÂõûÊåâÈíÆ -->
    <header class="detail-header">
      <button class="back-btn" @click="goBack">
        ‚Üê ËøîÂõû
      </button>
      <h1 class="page-title">‰∫ã‰ª∂ËØ¶ÊÉÖ</h1>
    </header>

    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <div v-if="loading" class="loading">
      <div class="loading-spinner">‚è≥</div>
      <p>Ê≠£Âú®Âä†ËΩΩ‰∫ã‰ª∂ËØ¶ÊÉÖ...</p>
    </div>

    <!-- ÈîôËØØÁä∂ÊÄÅ -->
    <div v-else-if="error" class="error-container">
      <div class="error-icon">‚ö†Ô∏è</div>
      <p>{{ error }}</p>
      <button class="retry-btn" @click="loadEventDetail">ÈáçËØï</button>
    </div>

    <!-- ‰∫ã‰ª∂‰ø°ÊÅØ -->
    <div class="event-container" v-else-if="event">
      <div class="event-header">
        <h2 class="event-title">{{ event.title }}</h2>
        <div class="event-meta-info">
          <span class="event-date">{{ formatDate(event.date) }}</span>
          <span class="event-age">{{ getAgeAtEvent(event.date) }}</span>
        </div>
      </div>

      <!-- ÁÖßÁâáÂ±ïÁ§∫ -->
      <div class="photos-section" v-if="event.media.images && event.media.images.length > 0">
        <h3 class="section-title">ÁÖßÁâá ({{ event.media.images.length }}Âº†)</h3>
        <div class="photos-grid">
          <div 
            v-for="(image, index) in event.media.images" 
            :key="index"
            class="photo-item"
            @click="openPhotoViewer(index)"
          >
            <div 
              class="photo-placeholder"
              :style="{ backgroundImage: `url(${getMediaUrl(event.id, image.fileName)})` }"
            >
              <span v-if="!image.fileName" class="photo-icon">üì∑</span>
              <span class="photo-index">{{ index + 1 }}</span>
            </div>
            <p class="media-desc">{{ image.desc }}</p>
          </div>
        </div>
      </div>

      <!-- ËßÜÈ¢ëÂ±ïÁ§∫ -->
      <div class="videos-section" v-if="event.media.videos && event.media.videos.length > 0">
        <h3 class="section-title">ËßÜÈ¢ë ({{ event.media.videos.length }}‰∏™)</h3>
        <div class="videos-grid">
          <div 
            v-for="(video, index) in event.media.videos" 
            :key="index"
            class="video-item"
          >
            <div class="video-container" @click="openVideoPlayer(video, index)">
              <video 
                :src="getMediaUrl(event.id, video.fileName)"
                class="video-preview"
                preload="metadata"
                @loadedmetadata="onVideoLoaded"
              >
                ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅËßÜÈ¢ëÊí≠Êîæ
              </video>
              <div class="video-overlay">
                <span class="play-overlay">‚ñ∂Ô∏è</span>
                <span class="video-duration" v-if="video.duration">{{ formatDuration(video.duration) }}</span>
              </div>
            </div>
            <p class="media-desc">{{ video.desc }}</p>
          </div>
        </div>
      </div>

      <!-- Èü≥È¢ëÊí≠Êîæ -->
      <div class="audio-section" v-if="event.media.audios && event.media.audios.length > 0">
        <h3 class="section-title">ÂΩïÈü≥ ({{ event.media.audios.length }}ÊÆµ)</h3>
        <div class="audio-list">
          <div 
            v-for="(audio, index) in event.media.audios" 
            :key="index"
            class="audio-item"
          >
            <div class="audio-player">
              <button class="play-btn" @click="toggleAudio(audio, index)">
                <span v-if="currentPlayingAudio === index && isAudioPlaying">‚è∏Ô∏è</span>
                <span v-else-if="currentPlayingAudio === index && isAudioLoading">‚è≥</span>
                <span v-else>‚ñ∂Ô∏è</span>
              </button>
              <div class="audio-info">
                <span class="audio-desc">{{ audio.desc }}</span>
                <div class="audio-progress" v-if="currentPlayingAudio === index">
                  <div class="progress-bar">
                    <div 
                      class="progress-fill" 
                      :style="{ width: audioProgress + '%' }"
                    ></div>
                  </div>
                  <span class="time-display">
                    {{ formatTime(audioCurrentTime) }} / {{ formatTime(audioDuration) }}
                  </span>
                </div>
              </div>
              <div class="audio-controls" v-if="currentPlayingAudio === index">
                <button class="volume-btn" @click="toggleMute">
                  {{ isAudioMuted ? 'üîá' : 'üîä' }}
                </button>
              </div>
            </div>
            <!-- ÈöêËóèÁöÑÈü≥È¢ëÂÖÉÁ¥† -->
            <audio 
              :ref="`audioPlayer${index}`"
              :src="getMediaUrl(event.id, audio.fileName)"
              @loadedmetadata="onAudioLoadedMetadata"
              @timeupdate="onAudioTimeUpdate"
              @ended="onAudioEnded"
              @error="onAudioError"
              @loadstart="onAudioLoadStart"
              @canplay="onAudioCanPlay"
              preload="metadata"
            ></audio>
          </div>
        </div>
      </div>

      <!-- ‰∫ã‰ª∂ÊèèËø∞ -->
      <div class="description-section">
        <h3 class="section-title">ËØ¶ÁªÜÊèèËø∞</h3>
        <p class="event-description">{{ event.description }}</p>
      </div>

      <!-- ÂÖ∂‰ªñ‰ø°ÊÅØ -->
      <div class="additional-info">
        <div class="info-item" v-if="event.location">
          <span class="info-label">Âú∞ÁÇπ:</span>
          <span class="info-value">{{ event.location }}</span>
        </div>
        <div class="info-item">
          <span class="info-label">ËÆ∞ÂΩïÊó∂Èó¥:</span>
          <span class="info-value">{{ formatDateTime(event.date) }}</span>
        </div>
      </div>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <div class="action-buttons">
        <button class="edit-btn" @click="editEvent">
          ‚úèÔ∏è ÁºñËæë
        </button>
        <button class="delete-btn" @click="deleteEvent">
          üóëÔ∏è Âà†Èô§
        </button>
      </div>
    </div>



    <!-- ÁÖßÁâáÊü•ÁúãÂô®Ê®°ÊÄÅÊ°Ü -->
    <div class="photo-modal" v-if="showPhotoViewer" @click="closePhotoViewer">
      <div class="modal-content" @click.stop>
        <button class="modal-close" @click="closePhotoViewer">‚úï</button>
        <div class="photo-viewer">
          <button class="nav-btn prev" @click="prevPhoto" v-if="currentPhotoIndex > 0">‚Äπ</button>
          <div class="current-photo">
            <div 
              class="large-photo-placeholder"
              :style="{ backgroundImage: `url(${getMediaUrl(event.id, event.media.images[currentPhotoIndex].fileName)})` }"
            >
              <span v-if="!event.media.images[currentPhotoIndex].fileName" class="photo-icon">üì∑</span>
            </div>
            <p class="photo-counter">{{ currentPhotoIndex + 1 }} / {{ event.media.images.length }}</p>
          </div>
          <button class="nav-btn next" @click="nextPhoto" v-if="currentPhotoIndex < event.media.images.length - 1">‚Ä∫</button>
        </div>
      </div>
    </div>

    <!-- ËßÜÈ¢ëÊí≠ÊîæÂô®Ê®°ÊÄÅÊ°Ü -->
    <div class="video-modal" v-if="showVideoPlayer" @click="closeVideoPlayer">
      <div class="modal-content" @click.stop>
        <button class="modal-close" @click="closeVideoPlayer">‚úï</button>
        <div class="video-player">
          <video 
            ref="videoPlayerRef"
            :src="getMediaUrl(event.id, currentVideo?.fileName)"
            class="modal-video"
            controls
            autoplay
            @ended="onVideoEnded"
            @error="onVideoError"
          >
            ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅËßÜÈ¢ëÊí≠Êîæ
          </video>
          <div class="video-info">
            <h3 class="video-title">{{ currentVideo?.desc || 'ËßÜÈ¢ë' }}</h3>
            <div class="video-controls">
              <button class="nav-btn prev" @click="prevVideo" v-if="currentVideoIndex > 0">‚Äπ ‰∏ä‰∏Ä‰∏™</button>
              <span class="video-counter">{{ currentVideoIndex + 1 }} / {{ event.media.videos.length }}</span>
              <button class="nav-btn next" @click="nextVideo" v-if="currentVideoIndex < event.media.videos.length - 1">‰∏ã‰∏Ä‰∏™ ‚Ä∫</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import dayjs from 'dayjs'
import { getEventById, deleteEvent, getMediaUrl } from '@/api/events'

export default {
  name: 'EventDetail',
  setup() {
    const route = useRoute()
    const router = useRouter()
    
    const event = ref(null)
    const isPlaying = ref(false)
    const showPhotoViewer = ref(false)
    const currentPhotoIndex = ref(0)
    const showVideoPlayer = ref(false)
    const currentVideoIndex = ref(0)
    const currentVideo = ref(null)
    const videoPlayerRef = ref(null)
    const currentPlayingAudio = ref(-1)
    const isAudioPlaying = ref(false)
    const isAudioLoading = ref(false)
    const isAudioMuted = ref(false)
    const audioProgress = ref(0)
    const audioCurrentTime = ref(0)
    const audioDuration = ref(0)
    const currentAudioElement = ref(null)
    const loading = ref(true)
    const error = ref('')

    // Ëé∑Âèñ‰∫ã‰ª∂ËØ¶ÊÉÖ
    const loadEventDetail = async () => {
      try {
        loading.value = true
        error.value = ''
        
        const eventId = route.params.id
        const response = await getEventById(eventId)
        
        if (response.success) {
          event.value = response.data
        } else {
          error.value = response.message || 'Ëé∑Âèñ‰∫ã‰ª∂ËØ¶ÊÉÖÂ§±Ë¥•'
          // Â¶ÇÊûú‰∫ã‰ª∂‰∏çÂ≠òÂú®Ôºå3ÁßíÂêéËøîÂõûÈ¶ñÈ°µ
          if (response.message === '‰∫ã‰ª∂‰∏çÂ≠òÂú®') {
            setTimeout(() => {
              router.push('/')
            }, 3000)
          }
        }
      } catch (err) {
        error.value = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï'
        console.error('Âä†ËΩΩ‰∫ã‰ª∂ËØ¶ÊÉÖÂ§±Ë¥•:', err)
      } finally {
        loading.value = false
      }
    }

    // Ê†ºÂºèÂåñÊó•Êúü
    const formatDate = (date) => {
      return dayjs(date).format('YYYYÂπ¥MMÊúàDDÊó•')
    }

    // Ê†ºÂºèÂåñÂÆåÊï¥Êó•ÊúüÊó∂Èó¥
    const formatDateTime = (date) => {
      return dayjs(date).format('YYYYÂπ¥MMÊúàDDÊó• HH:mm')
    }

    // ËÆ°ÁÆó‰∫ã‰ª∂ÂèëÁîüÊó∂ÂÆùÂÆùÁöÑÂπ¥ÈæÑ
    const getAgeAtEvent = (eventDate) => {
      const birth = dayjs('2025-05-09')
      const eventDay = dayjs(eventDate)
      const totalMonths = eventDay.diff(birth, 'month')
      
      if (totalMonths < 12) {
        const monthStart = birth.add(totalMonths, 'month')
        const days = eventDay.diff(monthStart, 'day')
        
        if (totalMonths === 0) {
          const totalDays = eventDay.diff(birth, 'day')
          return `Âá∫ÁîüÁ¨¨${totalDays}Â§©`
        } else {
          return `${totalMonths}‰∏™Êúà${days > 0 ? `${days}Â§©` : ''}`
        }
      } else {
        const years = Math.floor(totalMonths / 12)
        const remainingMonths = totalMonths % 12
        return `${years}Â≤Å${remainingMonths > 0 ? `${remainingMonths}‰∏™Êúà` : ''}`
      }
    }

    // ËøîÂõûÈ¶ñÈ°µ
    const goBack = () => {
      // ËøîÂõûÈ¶ñÈ°µÊó∂‰º†ÈÄíÂΩìÂâç‰∫ã‰ª∂IDÔºåÁî®‰∫éÂÆö‰Ωç
      router.push({
        path: '/',
        query: { highlight: event.value?.id }
      })
    }

    // ÊâìÂºÄÁÖßÁâáÊü•ÁúãÂô®
    const openPhotoViewer = (index) => {
      currentPhotoIndex.value = index
      showPhotoViewer.value = true
    }

    // ÂÖ≥Èó≠ÁÖßÁâáÊü•ÁúãÂô®
    const closePhotoViewer = () => {
      showPhotoViewer.value = false
    }

    // ‰∏ä‰∏ÄÂº†ÁÖßÁâá
    const prevPhoto = () => {
      if (currentPhotoIndex.value > 0) {
        currentPhotoIndex.value--
      }
    }

    // ‰∏ã‰∏ÄÂº†ÁÖßÁâá
    const nextPhoto = () => {
      if (currentPhotoIndex.value < event.value.media.images.length - 1) {
        currentPhotoIndex.value++
      }
    }

    // ÊâìÂºÄËßÜÈ¢ëÊí≠ÊîæÂô®
    const openVideoPlayer = (video, index) => {
      currentVideo.value = video
      currentVideoIndex.value = index
      showVideoPlayer.value = true
    }

    // ÂÖ≥Èó≠ËßÜÈ¢ëÊí≠ÊîæÂô®
    const closeVideoPlayer = () => {
      showVideoPlayer.value = false
      if (videoPlayerRef.value) {
        videoPlayerRef.value.pause()
      }
    }

    // ‰∏ä‰∏Ä‰∏™ËßÜÈ¢ë
    const prevVideo = () => {
      if (currentVideoIndex.value > 0) {
        currentVideoIndex.value--
        currentVideo.value = event.value.media.videos[currentVideoIndex.value]
      }
    }

    // ‰∏ã‰∏Ä‰∏™ËßÜÈ¢ë
    const nextVideo = () => {
      if (currentVideoIndex.value < event.value.media.videos.length - 1) {
        currentVideoIndex.value++
        currentVideo.value = event.value.media.videos[currentVideoIndex.value]
      }
    }

    // ËßÜÈ¢ëÂä†ËΩΩÂÆåÊàê
    const onVideoLoaded = (e) => {
      // Ëé∑ÂèñËßÜÈ¢ëÊó∂ÈïøÁ≠â‰ø°ÊÅØ
      const video = e.target
      if (video.duration) {
        console.log('ËßÜÈ¢ëÊó∂Èïø:', video.duration)
      }
    }

    // ËßÜÈ¢ëÊí≠ÊîæÁªìÊùü
    const onVideoEnded = () => {
      console.log('ËßÜÈ¢ëÊí≠ÊîæÁªìÊùü')
      // ÂèØ‰ª•Ëá™Âä®Êí≠Êîæ‰∏ã‰∏Ä‰∏™ËßÜÈ¢ë
      if (currentVideoIndex.value < event.value.media.videos.length - 1) {
        nextVideo()
      }
    }

    // ËßÜÈ¢ëÊí≠ÊîæÈîôËØØ
    const onVideoError = (e) => {
      console.error('ËßÜÈ¢ëÊí≠ÊîæÈîôËØØ:', e)
      alert('ËßÜÈ¢ëÊí≠ÊîæÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÊàñËÅîÁ≥ªÁÆ°ÁêÜÂëò')
    }

    // Ê†ºÂºèÂåñËßÜÈ¢ëÊó∂Èïø
    const formatDuration = (seconds) => {
      const mins = Math.floor(seconds / 60)
      const secs = Math.floor(seconds % 60)
      return `${mins}:${secs.toString().padStart(2, '0')}`
    }

    // Êí≠Êîæ/ÊöÇÂÅúÈü≥È¢ë
    const toggleAudio = async (audio, index) => {
      const audioElement = document.querySelector(`audio[src*="${audio.fileName}"]`)
      
      if (!audioElement) {
        console.error('Èü≥È¢ëÂÖÉÁ¥†‰∏çÂ≠òÂú®')
        return
      }

      // Â¶ÇÊûúÊ≠£Âú®Êí≠ÊîæÂêå‰∏Ä‰∏™Èü≥È¢ëÔºåÂàôÊöÇÂÅú
      if (currentPlayingAudio.value === index && isAudioPlaying.value) {
        audioElement.pause()
        isAudioPlaying.value = false
        return
      }

      // ÂÅúÊ≠¢‰πãÂâçÊí≠ÊîæÁöÑÈü≥È¢ë
      if (currentAudioElement.value && currentAudioElement.value !== audioElement) {
        currentAudioElement.value.pause()
        currentAudioElement.value.currentTime = 0
      }

      try {
        currentPlayingAudio.value = index
        currentAudioElement.value = audioElement
        isAudioLoading.value = true
        
        await audioElement.play()
        isAudioPlaying.value = true
        isAudioLoading.value = false
      } catch (error) {
        console.error('Èü≥È¢ëÊí≠ÊîæÂ§±Ë¥•:', error)
        isAudioLoading.value = false
        isAudioPlaying.value = false
        alert('Èü≥È¢ëÊí≠ÊîæÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•')
      }
    }

    // Èü≥È¢ëÂä†ËΩΩÂºÄÂßã
    const onAudioLoadStart = () => {
      isAudioLoading.value = true
    }

    // Èü≥È¢ëÂèØ‰ª•Êí≠Êîæ
    const onAudioCanPlay = () => {
      isAudioLoading.value = false
    }

    // Èü≥È¢ëÂÖÉÊï∞ÊçÆÂä†ËΩΩÂÆåÊàê
    const onAudioLoadedMetadata = (e) => {
      const audio = e.target
      audioDuration.value = audio.duration || 0
    }

    // Èü≥È¢ëÊó∂Èó¥Êõ¥Êñ∞
    const onAudioTimeUpdate = (e) => {
      const audio = e.target
      audioCurrentTime.value = audio.currentTime
      if (audioDuration.value > 0) {
        audioProgress.value = (audio.currentTime / audioDuration.value) * 100
      }
    }

    // Èü≥È¢ëÊí≠ÊîæÁªìÊùü
    const onAudioEnded = () => {
      isAudioPlaying.value = false
      currentPlayingAudio.value = -1
      audioProgress.value = 0
      audioCurrentTime.value = 0
      currentAudioElement.value = null
    }

    // Èü≥È¢ëÊí≠ÊîæÈîôËØØ
    const onAudioError = (e) => {
      console.error('Èü≥È¢ëÊí≠ÊîæÈîôËØØ:', e)
      isAudioPlaying.value = false
      isAudioLoading.value = false
      currentPlayingAudio.value = -1
      alert('Èü≥È¢ëÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•')
    }

    // ÂàáÊç¢ÈùôÈü≥
    const toggleMute = () => {
      if (currentAudioElement.value) {
        isAudioMuted.value = !isAudioMuted.value
        currentAudioElement.value.muted = isAudioMuted.value
      }
    }

    // Ê†ºÂºèÂåñÊó∂Èó¥ÊòæÁ§∫
    const formatTime = (seconds) => {
      if (!seconds || isNaN(seconds)) return '0:00'
      const mins = Math.floor(seconds / 60)
      const secs = Math.floor(seconds % 60)
      return `${mins}:${secs.toString().padStart(2, '0')}`
    }

    // ÁºñËæë‰∫ã‰ª∂
    const editEvent = () => {
      console.log('ÁºñËæë‰∫ã‰ª∂:', event.value.id)
      // Ë∑≥ËΩ¨Âà∞ÁºñËæëÈ°µÈù¢
      router.push(`/edit/${event.value.id}`)
    }

    // Âà†Èô§‰∫ã‰ª∂
    const handleDeleteEvent = async () => {
      if (confirm('Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™‰∫ã‰ª∂ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ')) {
        try {
          const response = await deleteEvent(event.value.id)
          if (response.success) {
            // Âà†Èô§ÊàêÂäüÔºåËøîÂõûÈ¶ñÈ°µÂπ∂‰º†ÈÄíÂ∑≤Âà†Èô§ÁöÑIDÔºåËÆ©È¶ñÈ°µÂÆö‰ΩçÂà∞ÊúÄÊé•ËøëÁöÑ‰∫ã‰ª∂
            router.push({
              path: '/',
              query: { highlight: event.value.id }
            })
          } else {
            alert(response.message || 'Âà†Èô§Â§±Ë¥•')
          }
        } catch (err) {
          alert('ÁΩëÁªúÈîôËØØÔºåÂà†Èô§Â§±Ë¥•')
          console.error('Âà†Èô§‰∫ã‰ª∂Â§±Ë¥•:', err)
        }
      }
    }

    onMounted(async () => {
      await loadEventDetail()
      // Á°Æ‰øùÈ°µÈù¢ÊªöÂä®Âà∞È°∂ÈÉ®
      window.scrollTo(0, 0)
    })

    return {
      event,
      isPlaying,
      showPhotoViewer,
      currentPhotoIndex,
      showVideoPlayer,
      currentVideoIndex,
      currentVideo,
      videoPlayerRef,
      currentPlayingAudio,
      isAudioPlaying,
      isAudioLoading,
      isAudioMuted,
      audioProgress,
      audioCurrentTime,
      audioDuration,
      currentAudioElement,
      loading,
      error,
      formatDate,
      formatDateTime,
      getAgeAtEvent,
      goBack,
      openPhotoViewer,
      closePhotoViewer,
      prevPhoto,
      nextPhoto,
      openVideoPlayer,
      closeVideoPlayer,
      prevVideo,
      nextVideo,
      onVideoLoaded,
      onVideoEnded,
      onVideoError,
      formatDuration,
      toggleAudio,
      onAudioLoadStart,
      onAudioCanPlay,
      onAudioLoadedMetadata,
      onAudioTimeUpdate,
      onAudioEnded,
      onAudioError,
      toggleMute,
      formatTime,
      editEvent,
      deleteEvent: handleDeleteEvent,
      loadEventDetail,
      getMediaUrl
    }
  }
}
</script>

<style scoped>
.event-detail {
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* Â§¥ÈÉ® */
.detail-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.back-btn {
