<template>
  <div class="home">
    <!-- Â§¥ÈÉ®‰ø°ÊÅØ -->
    <header class="header">
      <div class="baby-info">
        <div class="avatar">üë∂</div>
        <div class="info">
          <h1 class="baby-name">ÂàòÁü•ËÆ∏Â∞èÊúãÂèã</h1>
          <p class="baby-age">{{ currentAge }}</p>
        </div>
      </div>
      <div class="stats">
        <div class="stat-item">
          <div class="stat-number">{{ totalEvents }}</div>
          <div class="stat-label">‰∏™‰∫ã‰ª∂</div>
        </div>
        <div class="stat-item">
          <div class="stat-number">{{ totalPhotos }}</div>
          <div class="stat-label">Âº†ÁÖßÁâá</div>
        </div>
        <div class="stat-item">
          <div class="stat-number">{{ totalVideos }}</div>
          <div class="stat-label">‰∏™ËßÜÈ¢ë</div>
        </div>
        <div class="stat-item">
          <div class="stat-number">{{ totalAudios }}</div>
          <div class="stat-label">ÊÆµÂΩïÈü≥</div>
        </div>
      </div>
    </header>

    <!-- Êó∂Èó¥Á∫ø -->
    <div class="timeline-container">
      <div class="timeline-header">
        <h2 class="timeline-title">ÊàêÈïøÊó∂ÂÖâËΩ¥</h2>
        <div class="sort-control">
          <label class="sort-checkbox">
            <input 
              type="checkbox" 
              v-model="sortAscending" 
              @change="onSortChange"
            />
            <span class="checkmark"></span>
            <span class="sort-label">{{ sortAscending ? 'ÊúÄÊñ∞Âú®‰∏ã' : 'ÊúÄÊñ∞Âú®‰∏ä' }}</span>
          </label>
        </div>
      </div>
      
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <div v-if="loading" class="loading-container">
        <div class="loading-spinner">‚è≥</div>
        <p>Ê≠£Âú®Âä†ËΩΩÊàêÈïøËÆ∞ÂΩï...</p>
      </div>
      
      <!-- ÈîôËØØÁä∂ÊÄÅ -->
      <div v-else-if="error" class="error-container">
        <div class="error-icon">‚ö†Ô∏è</div>
        <p>{{ error }}</p>
        <button class="retry-btn" @click="loadEventsList">ÈáçËØï</button>
      </div>
      
      <!-- Êó∂Èó¥Á∫øÂÜÖÂÆπ -->
      <div v-else class="timeline">
        <div 
          v-for="(period, index) in sortedTimelinePeriods" 
          :key="index"
          class="timeline-period"
        >
          <div class="period-header">
            <div class="period-age">{{ period.age }}</div>
            <div class="period-date">{{ period.date }}</div>
          </div>
          <div class="events-grid">
            <div 
              v-for="event in period.events" 
              :key="event.id"
              class="event-card"
              :data-event-id="event.id"
              @click="viewEvent(event)"
            >
              <div class="event-photos">
                <!-- ÊòæÁ§∫ÂõæÁâá -->
                <LazyImage
                  v-for="(image, photoIndex) in getAllMediaItems(event).slice(0, 4)" 
                  :key="`${image.type}-${photoIndex}`"
                  :src="getMediaUrl(event.id, image.fileName, true)"
                  :alt="`${event.title} - ${image.type === 'video' ? 'ËßÜÈ¢ë' : 'ÂõæÁâá'}${photoIndex + 1}`"
                  :small="getAllMediaItems(event).length > 1"
                  :preload="shouldPreloadImage(index, photoIndex)"
                  :priority="getImagePriority(index, photoIndex)"
                  :threshold="loadingStrategy.threshold"
                  class="photo-item"
                  :class="{ 
                    'small': getAllMediaItems(event).length > 1,
                    'video-thumbnail': image.type === 'video'
                  }"
                />
                <div v-if="getAllMediaItems(event).length > 4" class="more-photos">
                  +{{ getAllMediaItems(event).length - 4 }}
                </div>
              </div>
              <div class="event-info">
                <h3 class="event-title">{{ event.title }}</h3>
                <p class="event-description">{{ event.description }}</p>
                <div class="event-meta">
                  <span v-if="event.media.audios.length > 0" class="audio-indicator">üéµ</span>
                  <span v-if="event.media.videos.length > 0" class="video-indicator">üé¨</span>
                  <span class="photo-count">{{ event.media.images.length }}Âº†ÁÖßÁâá</span>
                  <span v-if="event.media.videos.length > 0" class="video-count">{{ event.media.videos.length }}‰∏™ËßÜÈ¢ë</span>
                  <span v-if="event.media.audios.length > 0" class="audio-count">{{ event.media.audios.length }}ÊÆµÈü≥È¢ë</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Âø´ÈÄüÊ∑ªÂä†ÊåâÈíÆ -->
    <button class="fab" @click="addEvent">
      ‚ûï
    </button>

    <!-- ÊÄßËÉΩÁõëÊéßÈù¢ÊùøÔºà‰ªÖÂºÄÂèëÁéØÂ¢ÉÔºâ -->
    <PerformancePanel />
  </div>
</template>

<script>
import { ref, computed, onMounted, nextTick } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import dayjs from 'dayjs'
import { getEventsList, getStats, getMediaUrl } from '@/api/events'
import LazyImage from '@/components/LazyImage.vue'
import PerformancePanel from '@/components/PerformancePanel.vue'
import { preloadEventImages } from '@/utils/imageUtils'
import { adaptiveImageLoader } from '@/utils/imagePerformance'

export default {
  name: 'HomePage',
  components: {
    LazyImage,
    PerformancePanel
  },
  setup() {
    const router = useRouter()
    const route = useRoute()
    const babyBirthDate = ref(dayjs('2025-05-09')) // ÂÆùÂÆùÂá∫ÁîüÊó•ÊúüÔºö2025Âπ¥5Êúà9Êó•
    
    // ËÆ°ÁÆóÂΩìÂâçÂπ¥ÈæÑ
    const currentAge = computed(() => {
      const now = dayjs()
      const birth = babyBirthDate.value
      
      // Â¶ÇÊûúÂá∫ÁîüÊó•ÊúüÂú®Êú™Êù•ÔºåËøîÂõû"Âç≥Â∞ÜÂá∫Áîü"
      if (now.isBefore(birth)) {
        return 'Âç≥Â∞ÜÂá∫Áîü'
      }
      
      const totalMonths = now.diff(birth, 'month')
      
      if (totalMonths < 12) {
        // ËÆ°ÁÆóÂáÜÁ°ÆÁöÑÂ§©Êï∞ÔºöÂΩìÂâçÊó•ÊúüÂáèÂéªËøô‰∏™ÊúàÂºÄÂßãÊó∂ÂØπÂ∫îÁöÑÂá∫ÁîüÊúàÊó•
        const monthStart = birth.add(totalMonths, 'month')
        const days = now.diff(monthStart, 'day')
        
        if (totalMonths === 0) {
          const totalDays = now.diff(birth, 'day')
          return `${totalDays}Â§©`
        } else {
          return `${totalMonths}‰∏™Êúà${days > 0 ? ` ${days}Â§©` : ''}`
        }
      } else {
        const years = Math.floor(totalMonths / 12)
        const remainingMonths = totalMonths % 12
        return `${years}Â≤Å${remainingMonths > 0 ? ` ${remainingMonths}‰∏™Êúà` : ''}`
      }
    })

    // ‰ªélocalStorageËé∑ÂèñÊéíÂ∫èÁä∂ÊÄÅ
    const getSortPreference = () => {
      try {
        const saved = localStorage.getItem('baby-log-sort-ascending')
        return saved !== null ? JSON.parse(saved) : false
      } catch (error) {
        console.warn('Ëé∑ÂèñÊéíÂ∫èÂÅèÂ•ΩÂ§±Ë¥•:', error)
        return false
      }
    }

    // ‰øùÂ≠òÊéíÂ∫èÁä∂ÊÄÅÂà∞localStorage
    const saveSortPreference = (ascending) => {
      try {
        localStorage.setItem('baby-log-sort-ascending', JSON.stringify(ascending))
      } catch (error) {
        console.warn('‰øùÂ≠òÊéíÂ∫èÂÅèÂ•ΩÂ§±Ë¥•:', error)
      }
    }

    // ÂìçÂ∫îÂºèÊï∞ÊçÆ
    const timelinePeriods = ref([])
    const loading = ref(true)
    const error = ref('')
    const sortAscending = ref(getSortPreference()) // ‰ªélocalStorageËé∑ÂèñÂàùÂßãÁä∂ÊÄÅ
    const stats = ref({
      totalEvents: 0,
      totalPhotos: 0,
      totalVideos: 0,
      totalAudios: 0
    })

    // Ëé∑ÂèñËá™ÈÄÇÂ∫îÂä†ËΩΩÁ≠ñÁï•
    const loadingStrategy = adaptiveImageLoader.getLoadingStrategy()

    // Âä†ËΩΩ‰∫ã‰ª∂ÂàóË°®
    const loadEventsList = async () => {
      try {
        loading.value = true
        error.value = ''
        
        const response = await getEventsList()
        if (response.success) {
          timelinePeriods.value = response.data
          
          // È¢ÑÂä†ËΩΩÂÖ≥ÈîÆÂõæÁâáÔºàÊ†πÊçÆÁΩëÁªúÊÉÖÂÜµË∞ÉÊï¥Êï∞ÈáèÔºâ
          const allEvents = response.data.flatMap(period => period.events)
          if (allEvents.length > 0 && loadingStrategy.enablePreload) {
            preloadEventImages(allEvents, loadingStrategy.preloadCount, getMediaUrl)
          }
        } else {
          error.value = response.message || 'Ëé∑Âèñ‰∫ã‰ª∂ÂàóË°®Â§±Ë¥•'
        }
      } catch (err) {
        error.value = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï'
        console.error('Âä†ËΩΩ‰∫ã‰ª∂ÂàóË°®Â§±Ë¥•:', err)
      } finally {
        loading.value = false
      }
    }

    // Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆ
    const loadStats = async () => {
      try {
        const response = await getStats()
        if (response.success) {
          stats.value = response.data
        }
      } catch (err) {
        console.error('Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', err)
      }
    }

    // ËÆ°ÁÆóÁªüËÆ°Êï∞ÊçÆÔºà‰ΩøÁî®APIËé∑ÂèñÁöÑstatsÊï∞ÊçÆÔºâ
    const totalEvents = computed(() => stats.value.totalEvents)
    const totalPhotos = computed(() => stats.value.totalPhotos)
    const totalVideos = computed(() => stats.value.totalVideos)
    const totalAudios = computed(() => stats.value.totalAudios)

    // ËÆ°ÁÆóÊéíÂ∫èÂêéÁöÑÊó∂Èó¥Á∫øÊï∞ÊçÆ
    const sortedTimelinePeriods = computed(() => {
      if (!timelinePeriods.value || timelinePeriods.value.length === 0) {
        return []
      }
      
      const sorted = [...timelinePeriods.value]
      
      if (sortAscending.value) {
        // Ê≠£Â∫èÔºöÊúÄÊóßÁöÑÂú®‰∏äÔºåÊúÄÊñ∞ÁöÑÂú®‰∏ã
        return sorted.sort((a, b) => {
          // ‰ΩøÁî®period‰∏≠Á¨¨‰∏Ä‰∏™‰∫ã‰ª∂ÁöÑÊó•ÊúüËøõË°åÊéíÂ∫è
          const dateA = new Date(a.events[0]?.date || '1970-01-01')
          const dateB = new Date(b.events[0]?.date || '1970-01-01')
          return dateA - dateB
        })
      } else {
        // ÂÄíÂ∫èÔºöÊúÄÊñ∞ÁöÑÂú®‰∏äÔºåÊúÄÊóßÁöÑÂú®‰∏ãÔºàÈªòËÆ§Ôºâ
        return sorted.sort((a, b) => {
          const dateA = new Date(a.events[0]?.date || '1970-01-01')
          const dateB = new Date(b.events[0]?.date || '1970-01-01')
          return dateB - dateA
        })
      }
    })

    // ÊñπÊ≥ï
    const viewEvent = (event) => {
      console.log('Êü•Áúã‰∫ã‰ª∂:', event)
      // Ë∑≥ËΩ¨Âà∞‰∫ã‰ª∂ËØ¶ÊÉÖÈ°µ
      router.push(`/event/${event.id}`)
    }

    const addEvent = () => {
      console.log('Ê∑ªÂä†Êñ∞‰∫ã‰ª∂')
      // Ë∑≥ËΩ¨Âà∞Ê∑ªÂä†‰∫ã‰ª∂È°µÈù¢
      router.push('/add')
    }

    const onSortChange = () => {
      console.log('ÊéíÂ∫èÊñπÂºèÊîπÂèò:', sortAscending.value ? 'Ê≠£Â∫è' : 'ÂÄíÂ∫è')
      // ‰øùÂ≠òÁî®Êà∑ÁöÑÊéíÂ∫èÂÅèÂ•ΩÂà∞localStorage
      saveSortPreference(sortAscending.value)
    }

    // Âà§Êñ≠ÊòØÂê¶Â∫îËØ•È¢ÑÂä†ËΩΩÂõæÁâá
    const shouldPreloadImage = (eventIndex, photoIndex) => {
      // ‰∏∫Ââç2‰∏™‰∫ã‰ª∂ÁöÑÁ¨¨‰∏ÄÂº†ÂõæÁâáÂêØÁî®È¢ÑÂä†ËΩΩ
      return eventIndex < 2 && photoIndex === 0
    }

    // Ëé∑ÂèñÂõæÁâáÂä†ËΩΩ‰ºòÂÖàÁ∫ß
    const getImagePriority = (eventIndex, photoIndex) => {
      // Ââç3‰∏™‰∫ã‰ª∂ÁöÑÁ¨¨‰∏ÄÂº†ÂõæÁâá‰∏∫È´ò‰ºòÂÖàÁ∫ß
      if (eventIndex < 3 && photoIndex === 0) {
        return 'high'
      }
      // Ââç6‰∏™‰∫ã‰ª∂ÁöÑÂÖ∂‰ªñÂõæÁâá‰∏∫ÊôÆÈÄö‰ºòÂÖàÁ∫ß
      if (eventIndex < 6) {
        return 'normal'
      }
      // ÂÖ∂‰Ωô‰∏∫‰Ωé‰ºòÂÖàÁ∫ß
      return 'low'
    }

    // Ëé∑ÂèñÊâÄÊúâÂ™í‰ΩìÈ°πÁõÆÔºàÂõæÁâáÂíåËßÜÈ¢ëÁº©Áï•ÂõæÔºâ
    const getAllMediaItems = (event) => {
      const allMedia = []
      
      // Ê∑ªÂä†ÂõæÁâá
      if (event.media.images && event.media.images.length > 0) {
        event.media.images.forEach(image => {
          allMedia.push({
            ...image,
            type: 'image'
          })
        })
      }
      
      // Ê∑ªÂä†ËßÜÈ¢ëÔºà‰Ωú‰∏∫Áº©Áï•ÂõæÊòæÁ§∫Ôºâ
      if (event.media.videos && event.media.videos.length > 0) {
        event.media.videos.forEach(video => {
          allMedia.push({
            ...video,
            type: 'video'
          })
        })
      }
      
      return allMedia
    }

    // Ëá™Âä®ÂÆö‰ΩçÂà∞ÊåáÂÆö‰∫ã‰ª∂
    const scrollToEvent = async (eventId) => {
      await nextTick()
      
      // Êü•ÊâæÂåÖÂê´ËØ•‰∫ã‰ª∂ÁöÑÂç°ÁâáÂÖÉÁ¥†
      const eventCard = document.querySelector(`[data-event-id="${eventId}"]`)
      if (eventCard) {
        // ÊªöÂä®Âà∞ÊåáÂÆö‰ΩçÁΩÆÂπ∂È´ò‰∫ÆÊòæÁ§∫
        eventCard.scrollIntoView({ 
          behavior: 'smooth', 
          block: 'center' 
        })
      
      } else {
        console.warn('Êú™ÊâæÂà∞ÊåáÂÆöÁöÑ‰∫ã‰ª∂Âç°Áâá:', eventId)
      }
    }

    // Êü•ÊâæÊúÄÊñ∞‰∫ã‰ª∂ID
    const findLatestEventId = () => {
      if (timelinePeriods.value && timelinePeriods.value.length > 0) {
        // Ëé∑ÂèñÊâÄÊúâ‰∫ã‰ª∂Âπ∂ÊåâÊó•ÊúüÊéíÂ∫èÔºåÊâæÂà∞ÊúÄÊñ∞ÁöÑ
        const allEvents = timelinePeriods.value.flatMap(period => period.events)
        if (allEvents.length > 0) {
          const latestEvent = allEvents.sort((a, b) => new Date(b.date) - new Date(a.date))[0]
          return latestEvent.id
        }
      }
      return null
    }

    // Êü•ÊâæIDÊï∞Â≠óÊúÄÊé•ËøëÁöÑ‰∫ã‰ª∂
    const findClosestEventId = (targetId) => {
      if (!timelinePeriods.value || timelinePeriods.value.length === 0) {
        return null
      }

      const allEvents = timelinePeriods.value.flatMap(period => period.events)
      if (allEvents.length === 0) {
        return null
      }

      const targetIdNum = parseInt(targetId, 10)
      if (isNaN(targetIdNum)) {
        // Â¶ÇÊûúÁõÆÊ†áID‰∏çÊòØÊï∞Â≠óÔºåËøîÂõûÊúÄÊñ∞‰∫ã‰ª∂
        return findLatestEventId()
      }

      // ÊâæÂà∞IDÊï∞Â≠óÊúÄÊé•ËøëÁöÑ‰∫ã‰ª∂
      let closestEvent = allEvents[0]
      let minDifference = Math.abs(parseInt(closestEvent.id, 10) - targetIdNum)

      for (const event of allEvents) {
        const eventIdNum = parseInt(event.id, 10)
        if (!isNaN(eventIdNum)) {
          const difference = Math.abs(eventIdNum - targetIdNum)
          if (difference < minDifference) {
            minDifference = difference
            closestEvent = event
          }
        }
      }

      return closestEvent.id
    }

    // È°µÈù¢ÂàùÂßãÂåñ
    onMounted(async () => {
      await Promise.all([
        loadEventsList(),
        loadStats()
      ])
      
      // Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅÂÆö‰ΩçÂà∞ÁâπÂÆö‰∫ã‰ª∂
      const highlightId = route.query.highlight
      if (highlightId) {
        // Ê£ÄÊü•ËØ•‰∫ã‰ª∂ÊòØÂê¶Â≠òÂú®
        const allEvents = timelinePeriods.value.flatMap(period => period.events)
        const targetEvent = allEvents.find(event => String(event.id) === String(highlightId))
        
        if (targetEvent) {
          // ‰∫ã‰ª∂Â≠òÂú®ÔºåÂÆö‰ΩçÂà∞ËØ•‰∫ã‰ª∂
          scrollToEvent(highlightId)
        } else {
          // ‰∫ã‰ª∂‰∏çÂ≠òÂú®ÔºåÂØªÊâæIDÊï∞Â≠óÊúÄÊé•ËøëÁöÑ‰∫ã‰ª∂
          const closestEventId = findClosestEventId(highlightId)
          if (closestEventId) {
            scrollToEvent(closestEventId)
          }
        }
      }
    })

    return {
      currentAge,
      timelinePeriods,
      sortedTimelinePeriods,
      sortAscending,
      totalEvents,
      totalPhotos,
      totalVideos,
      totalAudios,
      loading,
      error,
      viewEvent,
      addEvent,
      loadEventsList,
      onSortChange,
      getSortPreference,
      saveSortPreference,
      getMediaUrl,
      scrollToEvent,
      findLatestEventId,
      findClosestEventId,
      shouldPreloadImage,
      getImagePriority,
      getAllMediaItems,
      loadingStrategy
    }
  }
}
</script>

<style scoped>
.home {
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* Â§¥ÈÉ®‰ø°ÊÅØ */
.header {
  background: white;
  border-radius: 20px;
  padding: 25px;
  margin: 0 auto 30px auto;
  max-width: 800px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.baby-info {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ff9a9e 0%, #fecfef 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  margin-right: 20px;
}

.baby-name {
  font-size: 28px;
  font-weight: 600;
  color: #2c3e50;
  margin: 0 0 5px 0;
}

.baby-age {
  font-size: 16px;
  color: #7f8c8d;
  margin: 0;
}

.stats {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.stat-item {
  text-align: center;
}

.stat-number {
  font-size: 32px;
  font-weight: 700;
  color: #e74c3c;
  line-height: 1;
}

.stat-label {
  font-size: 14px;
  color: #7f8c8d;
  margin-top: 5px;
}

/* Êó∂Èó¥Á∫ø */
.timeline-container {
  max-width: 800px;
  margin: 0 auto;
}

.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.timeline-title {
  font-size: 24px;
  font-weight: 600;
  color: #2c3e50;
  margin: 0;
}

.sort-control {
  display: flex;
  align-items: center;
}

.sort-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  color: #7f8c8d;
  user-select: none;
}

.sort-checkbox input[type="checkbox"] {
  display: none;
}

.checkmark {
  width: 18px;
  height: 18px;
  border: 2px solid #bdc3c7;
  border-radius: 4px;
  margin-right: 8px;
  position: relative;
  transition: all 0.3s ease;
  background: white;
}

.sort-checkbox input[type="checkbox"]:checked + .checkmark {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-color: #667eea;
}

.sort-checkbox input[type="checkbox"]:checked + .checkmark::after {
